<template>
  <div class="menu">
    <a v-for="menu in menus" :key="menu">{{ menu }}</a>
  </div>

  <!-- 문제> Event를 클릭하면 디스카운드 템플릿이 Nav 바 밑에 애니메이션으로 나타나도록 수행 -->
  
  <!-- <div class="discount">
    <h4>지금 결재하면 20% 할인</h4>
  </div> -->

<transition name="event">
  <Discount v-if="sale == 'Event'"/>
</transition>

<button @click="sortPriceAsc">가격 오름차순 정렬</button>
<button @click="sortPriceDesc">가격 내림차순 정렬</button>

  <!-- <Discount /> -->
  <!-- <div class="aniStart" :class="{ aniEnd : modalStatus }">
    <Modal @초기화="신고수[$event] = 0" @closeModal="modalStatus=false" :roomsInfo="roomsInfo" 
   :currentProductNo="currentProductNo" :modalStatus="modalStatus" 
   :신고수="신고수"/>
  </div> -->
  
  <transition name="fade">
    <Modal @초기화="신고수[$event] = 0" @closeModal="modalStatus=false" :roomsInfo="roomsInfo" 
    :currentProductNo="currentProductNo" :modalStatus="modalStatus" 
    :신고수="신고수"/>
  </transition>

  <Card  @openModal="modalStatus=true; currentProductNo=$event" 
    @신고="신고수[$event]++" v-for="room in roomsInfo" :key="room" 
    :room="room" :신고수="신고수" :modalStatus="modalStatus"/>

</template>

<script>
import data from "./assets/oneroom.js";
import Modal from "./components/Modal.vue";
import Card from "./components/Card.vue";
import Discount from "./components/Discount.vue";

export default {
  name: 'App',
  data(){
    return{
      sale : '',
      modalStatus : false,
      currentProductNo : 0,
      origin : [...data],
      roomsInfo : data,
      price : [50,60,70],
      products : ['역삼동원룸', '천호동원룸','마포구원룸'],
      menus : ['Home', 'Shop', 'Event'],
      신고수 : [0,0,0,0,0,0],
    }
  },
  components : {
    Modal,
    Card,
    Discount,
  },
  methods : {
    sortTest(){
      let array = [3,5,2];
      array.sort();
      console('오름차순 정렬 : '+ array);

      array.sort(function(a,b){
        return b-a;
      })
      console.log('내림차순 정렬 : '+ array);
    },
    // 숫자 정렬
   
    sortPriceAsc(){
      this.roomsInfo.sort(function(a,b){
        return a.price - b.price;
      })
    },
    sortPriceDesc(){
      this.roomsInfo.sort(function(a,b){
        return b.price - a.price;
      })
    },
    dataOrigin(){
      this.roomsInfo = [...this.origin];
    },
    sortTitleAsc(){
      this.roomsInfo.sort((a,b)=>{
        if(a.title < b.title) return -1;
        if(a.title > b.title) return 1;
        else return 0;
      })
    },
    sortTitleDesc(){
      this.roomsInfo.sort((a,b)=>{
        if(a.title < b.title) return -1;
        if(a.title > b.title) return 1;
        else return 0;
      })
    },

  }
}
</script>

